import InputArea from "../../plugin/components/InputArea";

export default {
  name: "device_blacklist",
  components: {
    InputArea
  },
  data() {
    return {
      disabled: false,
      device_id: "",
      currentPage: 1,
      pagesize: 10,
      total: 0,
      dialogTitle: "",
      formLabelWidth: "120px",
      dialogFormVisible: false,
      dialogVisible: false,
      tableData: [],
      form: {
        forbid_device: "",
        type: "1",
        remark: ""
      },
      fileList: [],
      imageUrl: ""
    };
  },
  filters: {
    formatStatus: function(val) {
      return val === 1 ? "解禁" : "封号";
    }
  },
  methods: {
    resetForm() {
      this.form = {
        forbid_device: "",
        type: "1",
        remark: ""
      };
    },

    getDeviceBlackList() {
      this.$http
        .get("v1/backend/sys-conf/device-blacklist", {
          params: {
            page: this.currentPage,
            limit: this.pageSize
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
          }
        });
    },
    searchData() {
      this.$http
        .get("v1/backend/sys-conf/device-blacklist", {
          params: {
            page: this.currentPage,
            limit: this.pageSize,
            device_id: this.device_id
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
          }
        });
    },
    openAddDialog() {
      this.resetForm();
      this.disabled = false
      this.dialogTitle = "添加控制名单";
      this.dialogFormVisible = true;
    },
    addDeviceBlack() {
        if (!this.form.id) {
        let data = {
          ip: this.form.forbid_device,
          op_type: Number(this.form.type),
          remarks: this.form.remark
        };
        this.$http.post("v1/backend/sys-conf/device-blacklist", data).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.dialogFormVisible = false;
            this.getDeviceBlackList();
            this.$message({
              type: "success",
              message: res.data.msg
            });
          }
        });
      } else {
        let data = {
          id: this.form.id,
          ip: this.form.forbid_device,
          op_type: Number(this.form.type),
          remarks: this.form.remark
        };
        this.$http.post("v1/backend/sys-conf/device-blacklist", data).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.dialogFormVisible = false;
            this.getDeviceBlackList();
            this.$message({
              type: "success",
              message: res.data.msg
            });
          }
        });
      }
    },
    handleEdit(row) {
      console.log(row);
      this.dialogTitle = "编辑";
      this.dialogFormVisible = true;
      this.form.id = row.id;
      this.form.forbid_device = row.lock_data;
      this.form.type = String(row.lock_status);
      this.form.remark = row.memo;
    },
    change() {
      if (this.form.id) {
        this.disabled = true;
      }
    },
    updateStatus(row) {
      console.log(row);
      let data = {
        id: row.id,
        lock_status: row.lock_status === 0 ? 1 : 0
      };
      this.$http.put("v1/backend/sys-conf/device-blacklist", data).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          this.getDeviceBlackList();
          this.$message({
            type: "success",
            message: res.data.msg
          });
        }
      });
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getDeviceBlackList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getDeviceBlackList();
    }
  },
  mounted() {
    this.getDeviceBlackList();
  }
};



